}
@media screen and (max-width: 450px) {
  .tech {
    margin-top: 90px;
    align-items: center;
  }
  .tech-h1 {
    margin: 0 !important;
  }
  .tech__bottom {
    flex-direction: column;
    margin-top: 4rem;
  }
  .numbers {
    width: 80px;
    height: 80px;
  }
  .info__tech {
    width: 72%;
  }
  .info__tech p:nth-child(2) {
